html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--Very-Dark-Blue-BG);
  color: var(--White-Text);
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  margin-bottom: 0.1875rem;
  font-size: 1.5rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --Lime-Green: hsl(163, 72%, 41%);
  --Bright-Red: hsl(356, 69%, 56%);
  --Facebook: hsl(208, 92%, 53%);
  --Twitter: hsl(203, 89%, 53%);
  --Instagram-Start: hsl(37, 97%, 70%);
  --Instagram-End:hsl(329, 70%, 58%);
  --YouTube: hsl(348, 97%, 39%);
  --Toggle-bg-End: hsl(210, 78%, 56%);
  --Toggle-bg-Start: hsl(146, 68%, 55%);
  --Toggle: hsl(230, 22%, 74%);
  --Very-Dark-Blue-BG: hsl(230, 17%, 14%);
  --Very-Dark-Blue-Top-BG-Pattern: hsl(232, 19%, 15%);
  --Dark-Desaturated-Blue-Card-BG: hsl(228, 28%, 20%);
  --Desaturated-Blue-Text: hsl(228, 34%, 66%);
  --White-Text: hsl(0, 0%, 100%);
  --White-BG: hsl(0, 0%, 100%);
  --Very-Pale-Blue-Top-BG-Pattern: hsl(225, 100%, 98%);
  --Light-Grayish-Blue-Card-BG: hsl(227, 47%, 96%);
  --Dark-Grayish-Blue-Text: hsl(228, 12%, 44%);
  --Very-Dark-Blue-Text: hsl(230, 17%, 14%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

.container {
  max-width: 69.375rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: centerwwwaw;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--Desaturated-Blue-Text);
}
.toggle {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  border: none;
}
.toggle label {
  text-align: center;
}
.toggle label[for=dark] {
  grid-column: 1/2;
}
.toggle label[for=system] {
  grid-column: 2/3;
  grid-row: 2/3;
}
.toggle label[for=light] {
  grid-column: 3/4;
}
.toggle__wrapper {
  height: 1.5rem;
  grid-column: 2/3;
  grid-row: 1/2;
  position: relative;
}
.toggle input[type=radio] {
  opacity: 0;
}
.toggle__background {
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  overflow: hidden;
  background: linear-gradient(225deg, var(--Toggle-bg-Start) 0%, var(--Toggle-bg-End) 98.02%);
}
.toggle__button {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: var(--toggle);
}

.card {
  padding: 1.25rem;
}